body {
  margin: 0;
}

.header1, .header2 {
  background-color: #fff;
  justify-content: flex-end;
  align-items: center;
  width: 1200px;
  height: 60px;
  display: flex;
  position: relative;
}

.menu {
  flex-direction: row;
  display: flex;
  position: relative;
}

.item {
  color: #000;
  margin: 0 10px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
}

.item-apple, .item-laptops {
  color: #00acdc;
}

.item-apple.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: -23px;
}

img {
  width: 40px;
  height: 40px;
  display: block;
  position: absolute;
  top: 10px;
  left: 50px;
}

@media screen and (width >= 1024px) {
  .header2 {
    width: 1024px;
    height: 60px;
  }
}
